import React, { useState, useEffect } from "react";
import axios from "axios";
import { v4 as uuidv4 } from "uuid";
import { BrowserRouter as Router, Route } from "react-router-dom";
import git from "../src/github.svg"
import logo from "../src/logo.png"

import Header from "./components/Header";
import Tasks from "./components/Tasks";
import AddTask from "./components/AddTask";


import "./App.css";

const App = () => {
	const [tasks, setTasks] = useState([
		{
			id: "1",
			title: "Ver e seguir o perfil do Lucas no GitHub",
			completed: false,
		}
	]);

	const handleTaskClick = (taskId) => {
		const newTasks = tasks.map((task) => {
			if (task.id === taskId) return { ...task, completed: !task.completed };

			return task;
		});

		setTasks(newTasks);
	};

	const handleTaskAddition = (taskTitle) => {
		const newTasks = [
			...tasks,
			{
				title: taskTitle,
				id: uuidv4(),
				completed: false,
			},
		];

		setTasks(newTasks);
	};

	const handleTaskDeletion = (taskId) => {
		const newTasks = tasks.filter((task) => task.id !== taskId);

		setTasks(newTasks);
	};

	return (
		<>
			<div className="container">
				<Header />
				<AddTask handleTaskAddition={handleTaskAddition} />
				<Tasks
					tasks={tasks}
					handleTaskClick={handleTaskClick}
					handleTaskDeletion={handleTaskDeletion}
				/>
				<p>TIP : Clique no <strong>nome da Tarefa</strong> para marcar ela como concluida</p>
			</div>
			<div>
				<footer className="nav">
					<a className="a-logo" href="https://maiklord.github.io/React-ToDo/">
						<img className="logo" src={logo} />
					</a>
					<a className="a-git" href="http://github.com/maiklord">
						<img className="git" src={git} />
					</a>
				</footer>
			</div>
		</>
	);

};
export default App;
